import React, {
  useState,
  useRef,
  useEffect,
  useMemo,
} from "react";
import st from "./trnsformrer04kV.module.css";
import { OrganizationName } from "../../../Common/OrganizationName/organizarionName";
import { v1 } from "uuid";
import { EditableSpan } from "../../../Common/EditableSpan/EditableSpan";
import { TransformersTable04kV } from "./TransformerTable/transformersTable";
import { useReactToPrint } from "react-to-print";
import { DataInput } from "../../../Common/DataInput/DataInput";
import { ConfirmModal } from "../../../Common/ModalWindow/ModalWindow";
import { useDownloadExcel } from "react-export-table-to-excel";
import { Button } from "../../../Common/Button/Button";

type TransformerGroupType = {
  transformerName: string;
  cofficient: string;
  transformers: transformerType[];
};


type standarsTransformersType = {
  id: string
  nameType: string
  serialNumber: string
  accuracy: string
  verificationDate: string
};

export type transformerType = {
  id: string
  transformerName: string
  serialNumber: string
  cofficient: string
  accuracy: string
  load: string
  look: string
  result: string
  stigma: string
  data: string[]
};

export type ReportTransformer04kV05sType = {
  reportNumber: string
  organizationName: string
  bill: string
  date: string
  temperature: string
  humidity: string
  pressure: string
  voltage: string
  hz: string
  eginear: string
  standarts: standarsTransformersType[]
  transformer: transformerType[]
};

export const ReportTransformer04kV05s = () => {
  // const [pageCount, setPageCount] = useState<number>(1);
  const currentPrintRef = useRef<HTMLDivElement | null>(null);
  const currentExelRef = useRef<HTMLDivElement | null>(null);

  const tableRefs = useRef<{ [key: number]: HTMLTableElement | null }>({});

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isExporting, setIsExporting] = useState(false);
  const [lastPage, setLastPage] = useState(2);
  const [sortedTransformers, setSortedTransformers] = useState<TransformerGroupType[]>([])
  const [disabled, setDisabled] = useState<boolean>(true)
  const [index, setIndex] = useState<number>(0)
 
  // const componentRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});

   //   (  )
   const printHandler = useReactToPrint({
    content: () => currentPrintRef.current,
    documentTitle: "",
  });

  const handlePrint = () => {
    setTimeout(() => printHandler(), 0);

  };

   const useExcelExport = (ref: React.RefObject<HTMLTableElement | HTMLDivElement>, fileName: string) => {
    const { onDownload } = useDownloadExcel({
      currentTableRef: ref.current,
      filename: fileName,
      sheet: "",
    });
  
    return onDownload;
  };
  
  const [report, setReport] = useState<ReportTransformer04kV05sType>(() => {
    const saved = localStorage.getItem("reportTransformer");
    if (saved) {
      try {
        return JSON.parse(saved);
      } catch (e) {
        console.warn("  localStorage:", e);
      }
    }

    return {
      reportNumber: "1111/10/2160",
      organizationName:
        ' "  "  ""',
      bill: "2160-1111",
      date: "11.11.2025",
      temperature: "20,4",
      humidity: "38,9",
      pressure: "99,5",
      voltage: "228,9",
      hz: "50,0",
      eginear: " I  .. ",
      standarts: [
        {
          id: v1(),
          nameType: "  535/2",
          serialNumber: "117",
          accuracy: "0,02",
          verificationDate: "11.11.2025",
        },
        {
          id: v1(),
          nameType: " 507",
          serialNumber: "826",
          accuracy: "0,005 %",
          verificationDate: "11.11.2025",
        },
        {
          id: v1(),
          nameType: "  5018-5",
          serialNumber: "257",
          accuracy: "4 %",
          verificationDate: "11.11.2025",
        },
      ],
      transformer: [
        {
          id: v1(),
          transformerName: "-0,66-1-5-3",
          serialNumber: "1234567891011",
          cofficient: "300/5",
          accuracy: "0,5s",
          load: "5",
          look: "",
          result: "",
          stigma: "15000000",
          data: [
            "0,63",
            "88",
            "0,35",
            "8",
            "0,11",
            "5",
            "0,08",
            "4",
            "0,05",
            "4",
            "0.33",
            "7",
          ],
        },
      ],
    };
  });

  useEffect(() => {
    localStorage.setItem("reportTransformer", JSON.stringify(report));
  }, [report]);


const addTransformer = (data: number[]) => {
let dataForTransformer = data.map(el => el.toString().replace(".", ","))
setReport(prev => {
  const last = prev.transformer[prev.transformer.length - 1];

  const newStigma = last && !isNaN(Number(last.stigma))
    ? String(Number(last.stigma) + 1)
    : "1"; 

  return {
    ...prev,
    transformer: [
      ...prev.transformer,
      {
        ...last,
        id: v1(),
        serialNumber: "",
        stigma: newStigma,
        data: dataForTransformer,
      },
    ],
  };
});
}

const changeReportTitle = (key: string, title: string) => {
  setReport(prev => {
    const updated = {
      ...prev,
      [key]: title,
    };
    localStorage.setItem("report", JSON.stringify(updated)); 
    return updated;
  });
}

const removeTransformer = (id: string) => {
  setReport(prev => {
    const updated = {
      ...prev,
      transformer: prev.transformer.filter(t => t.id !== id),
    };
    localStorage.setItem("report", JSON.stringify(updated));
    return updated;
  });
}

const changeTransformerTitle = (id: string, key: string, title: string) => {
  setReport(prev => {
    const updated = {
      ...prev,
      transformer: prev.transformer.map(t =>
        t.id === id ? { ...t, [key]: title } : t
      ),
    };

    localStorage.setItem("report", JSON.stringify(updated));

    return updated;
  });
}

const changeTransformersData = (id: string, index: number, newValue: string) => {
  setReport(prev => {
    const updated = {
      ...prev,
      transformer: prev.transformer.map(t => {
        if (t.id === id) {
          const updatedData = [...t.data];
          updatedData[index] = newValue;
          return { ...t, data: updatedData };
        }
        return t;
      }),
    };

    localStorage.setItem("report", JSON.stringify(updated));
    return updated;
  });
}


const changeStandartsDate = (id: string, value: string) => {
  setReport(prev => {
    const updated = {
      ...prev,
      standarts: prev.standarts.map(s =>
        s.id === id ? { ...s, verificationDate: value } : s
      ),
    };

    localStorage.setItem("report", JSON.stringify(updated));
    return updated;
  });

}

const removeAlltransformers = () => {
  const initialTransformer = {
    id: v1(),
    transformerName: "-0,66-1-5-3",
    serialNumber: "",
    cofficient: "300/5",
    accuracy: "0,5s",
    load: "5",
    look: "",
    result: "",
    stigma: "15000000",
    data: [
      "0,63", "88", "0,35", "8", "0,11", "5",
      "0,08", "4", "0,05", "4", "0.33", "7",
    ],
  };

  setReport(prev => {
    const updated = {
      ...prev,
      transformer: [initialTransformer], //    
    };
    localStorage.setItem("report", JSON.stringify(updated));
    return updated;
  });
  setIsModalOpen(false)
}

  const rowsPerPage = 30;

  //    
  const transformerPages = useMemo(() => {
    const chunks = [];
    for (let i = 0; i < report.transformer.length; i += rowsPerPage) {
      chunks.push(report.transformer.slice(i, i + rowsPerPage));
    }
    return chunks;
  }, [report.transformer]);

  const pageCounts: number = transformerPages.length;

  // ..............................
  const exportToExcel = useExcelExport(currentExelRef, "_");

  const handleExportExcel = () => {
    setIsExporting(true);

    setTimeout(() => {
      exportToExcel(); 
      setIsExporting(false);
    }, 300);
  };

  const typeSort = () => {

    if (!report?.transformer || report.transformer.length === 0) return [];

    const groups: Record<string, transformerType[]> = {}; 
  
    report.transformer.forEach(t => {
      const name = t.transformerName.trim();
      const coef = t.cofficient.trim();
      const key = `${name}_${coef}`;
      if (!groups[key]) {
        groups[key] = [];
      }
      groups[key].push(t); 
    });
  
    setSortedTransformers(
      Object.entries(groups).map(([key, items]) => {
        const [transformerName, cofficient] = key.split("_");
        return {
          transformerName,
          cofficient,
          transformers: items,
        };
      })
    )
  }


  const saveTableForExport = (j: number) => {
    setIndex(j);
    setDisabled(false);
    alert(` ${j + 1}   `);
  };

  const onDownloadHandler = (mod: string, coef: string) => {
    const filename = `_${mod}_${coef}.xlsx`;
    onDownload();
    setDisabled(true);
  } 
  
 
  const { onDownload } = useDownloadExcel({
    currentTableRef: index !== null ? tableRefs.current[index] : null,
    filename: "", 
    sheet: "Data",
  });
  

  
  return (
    <div ref={currentPrintRef} className={st.pageWrapper}>
      <div  className={st.page}>
        <div>
          <div>
            <OrganizationName />
          </div>
          <div className={st.pageNumber}>
   {1}  {lastPage}
    </div>
        </div>
        <div className={st.protocolTitle}>
              {" "}
          <EditableSpan title={report.reportNumber} changeTitle={(title) => {changeReportTitle('reportNumber', title)}} />
        </div>
        <div className={st.title}>
          <div className={st.el}>
              :{" "}
            <EditableSpan
              title={report.organizationName}
              changeTitle={(title) => {changeReportTitle('organizationName', title)}}
            />
          </div>
          <div className={st.el}>
            H -:{" "}
            <EditableSpan title={report.bill} changeTitle={(title) => {changeReportTitle('bill', title)}} />
          </div>
          <div className={st.el}>
               /:{" "}
            <EditableSpan title={report.date} changeTitle={(title) => {changeReportTitle('date', title)}} />
          </div>
          <div className={st.el}>
              : -{" "}
            
          </div>
          <div className={st.el}>
              , C -{" "}
            <EditableSpan title={report.temperature} changeTitle={(title) => {changeReportTitle('temperature', title)}} />
          </div>
          <div className={st.el}>
              , % -{" "}
            <EditableSpan title={report.humidity} changeTitle={(title) => {changeReportTitle('humidity', title)}} />
          </div>
          <div className={st.el}>
             ,  -{" "}
            <EditableSpan title={report.pressure} changeTitle={(title) => {changeReportTitle('pressure', title)}} />
          </div>
          <div className={st.el}>
              ,  -{" "}
            <EditableSpan title={report.voltage} changeTitle={(title) => {changeReportTitle('voltage', title)}} />
          </div>
          <div className={st.el}>
              ,  -{" "}
            <EditableSpan title={report.hz} changeTitle={(title) => {changeReportTitle('hz', title)}} />
          </div>
          <div className={st.el}>
            :  8.217-2003 " .  "
          </div>
          <div className={st.el}>
            :
            <table border={1} className={st.standartTable}>
              <thead>
                <tr>
                  <th>   </th>
                  <th> </th>
                  <th>  / </th>
                  <th>  </th>
                </tr>
              </thead>
              <tbody>
                {report.standarts.map((el) => {
                  return (
                    <tr key={el.id}>
                      <td>{el.nameType}</td>
                      <td>{el.serialNumber}</td>
                      <td>{el.accuracy}</td>
                      <td>
                        <EditableSpan
                          title={el.verificationDate}
                          changeTitle={(title) => {changeStandartsDate(el.id, title)}}
                        />
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
            <button onClick={() => setIsModalOpen(true)}> </button>
            <button onClick={handleExportExcel}>   Excel</button>
           {/*  */}
            <button onClick={typeSort}>    </button>
            {isModalOpen && (
      <ConfirmModal
        title="  "
        message=" ,     ?"
        onConfirm={removeAlltransformers}
        onCancel={() => setIsModalOpen(false)}
      />
    )}
          </div>
        </div>
      </div>
      <div >
      {transformerPages.map((pageData, pageIndex) => (
  <div ref={currentExelRef} key={pageIndex} className={st.page}>
   
    <TransformersTable04kV
    isExporting={isExporting}
    report={report}
     setLastPage={setLastPage}
      lastPage={lastPage}
      pageData={pageData}
      pageIndex={pageIndex}
      pageCount={pageCounts}
      rowsPerPage={rowsPerPage}
      removeTransformer={removeTransformer}
      changeTransformerTitle={changeTransformerTitle}
      changeTransformersData={changeTransformersData}
      
    />
     <button onClick={() => handlePrint()}></button>
        <DataInput  setData={addTransformer}/>
        <div>
          ___________________ <EditableSpan title={report.eginear} changeTitle={(title) => {changeReportTitle('eginear', title)}} />
      </div>
  </div>
))}  
<div>
{sortedTransformers.length > 0 ? (
  sortedTransformers.map((el, i) => (
    <div key={i}>
      {/* <p>{`${el.transformerName}  ${el.cofficient}`}</p> */}

      <table ref={(el) => (tableRefs.current[i] = el)}>
        <thead>
          <tr>
          {`${el.transformerName}  ${el.cofficient}`}
          </tr>
        </thead>
        <tbody>
          {el.transformers.map((t, j) => (
           
            <tr key={j}>
              <td>{t.stigma}</td>
              <td>{t.serialNumber}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <Button disabled={false} title={"C"} onClick={()=> {saveTableForExport(i)}}/>
      <Button disabled={disabled} title={" Exel"} onClick={() => {onDownloadHandler(el.transformerName, el.cofficient)}}/>
    </div>
  ))
) : []}
</div>
      </div>
      
    </div>
  );
};
